import React from 'react';
import styles from './Headline.css';
import classnames from 'classnames';

type Props = {
  className?: string;
  children: any;
  level?: number;
  as?: number;
};

let LEVEL_CLASSES = [
  styles.levelOne,
  styles.levelTwo,
  styles.levelThree,
  styles.levelFour,
  styles.levelFive,
];

export default function Headline({
  className,
  children,
  level = 2,
  as = level,
}: Props) {
  let Tag: any = `h${as}`;

  return (
    <Tag
      className={classnames(
        LEVEL_CLASSES[level - 1],
        styles.headline,
        className,
      )}
    >
      {children}
    </Tag>
  );
}
